import articleList from '@/components/articleList.vue';
import activityList from '@/components/activityList.vue';
export default {
	data() {
		return {
			id: -1,
			info: null,
			type: 0,
			tabs: [
				{
					name: '资讯',
					url: 'admin_user/getAdminUserNews',
					list: [],
					pageNum: 1,
					pageSize: 10,
					loading: true,
					more: false
				},
				{
					name: '活动',
					url: 'admin_user/getAdminActivity',
					list: [],
					pageNum: 1,
					pageSize: 10,
					loading: true,
					more: false
				}
			]
		};
	},
	components: { articleList, activityList },
	onLoad(opt) {
		if (opt.id) {
			this.id = opt.id;
			this.$Fetch({
				url: 'admin_user/getDataById',
				data: { id: opt.id },
				success: res => {
					this.info = res;
				}
			});
			this.getList();
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		onChange(e) {
			this.type = e.detail.index;
			if(this.tabs[e.detail.index].list.length===0)
			this.getList();
		},
		getList() {
			let tabs = this.tabs;
			let type=this.type;
			if(tabs[type].loading){
				tabs[type].loading=false;
				this.tabs=tabs;
				this.$Fetch({
					url:tabs[type].url,
					data:{
						id:this.id,
						pageNum:tabs[type].pageNum,
						pageSize:tabs[type].pageSize
					},
					success:res=>{
						let len=res.length;
						if(len>0){
							tabs[type].list=tabs[type].list.concat(res);
							if(len===tabs[type].pageSize){
								tabs[type].pageNum++;
								tabs[type].loading=true;
								tabs[type].more=true;
							}else{
								tabs[type].more=false;
							}
							this.tabs=tabs;
						}
					}
				})
			}
		}
	}
};
